import {mapMutations } from 'vuex';
export default {
	data() {
		return {
			userInfo:{
				phone:"",
				nickname:"",
			}
		}
	},
	onShow() {
		this.getUserInfo();
	},
	methods: {
		//获取用户的信息
		getUserInfo(){
			let that=this;
			that.basePost(
				that.U({ c: 'user_api', a: 'my'}),
				{},
				function(res) {
					that.userInfo=res.data
				},
				function(res) {
					console.log(res);
				}
			);
		},
		edit() {
			uni.navigateTo({
				url: '/pages/user/info_edit/info_edit',
			});
		},
		address(e) {
			uni.navigateTo({
				url: '/pages/user/admin_address/admin_address',
			});
		},
		outLogin(){
			let that=this;
			uni.showModal({
				title: '提示',
				content: '确定退出当前用户？',
				success: function(res) {
					if (res.confirm) {
						that.logoutStore();
						uni.clearStorageSync("data");
						uni.redirectTo({
							url:'/pages/login/pwd_login/pwd_login'
						})
					} else{
						return;
					}
				}
			});
		},
		...mapMutations(['logoutStore'])
	}
}
